import { createClient } from "@/lib/supabase/server"
import { AnalyticsCharts } from "@/components/analytics-charts"

export default async function AnalyticsPage() {
  const supabase = await createClient()

  // Get books by genre
  const { data: books } = await supabase.from("books").select("genre")

  // Get authors with book counts
  const { data: authors } = await supabase.from("authors").select(`
      name,
      books:books(id)
    `)

  // Get books by decade
  const { data: booksByYear } = await supabase.from("books").select("published_year")

  // Process genre data
  const genreCounts: Record<string, number> = {}
  books?.forEach((book) => {
    if (book.genre) {
      genreCounts[book.genre] = (genreCounts[book.genre] || 0) + 1
    }
  })
  const genreData = Object.entries(genreCounts).map(([name, value]) => ({
    name,
    value,
  }))

  // Process author data
  const authorData =
    authors?.map((author) => ({
      name: author.name,
      books: (author.books as { id: string }[])?.length || 0,
    })) || []

  // Process decade data
  const decadeCounts: Record<string, number> = {}
  booksByYear?.forEach((book) => {
    if (book.published_year) {
      const decade = Math.floor(book.published_year / 10) * 10
      const decadeLabel = `${decade}s`
      decadeCounts[decadeLabel] = (decadeCounts[decadeLabel] || 0) + 1
    }
  })
  const decadeData = Object.entries(decadeCounts)
    .map(([decade, count]) => ({ decade, count }))
    .sort((a, b) => a.decade.localeCompare(b.decade))

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-foreground mb-2">Library Analytics</h1>
        <p className="text-muted-foreground">Insights into our book collection and authors</p>
      </div>

      <AnalyticsCharts genreData={genreData} authorData={authorData} decadeData={decadeData} />
    </div>
  )
}
